// @flow
import  React, { useState, useEffect } from 'react';
import css from './Paginator.module.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowAltCircleLeft , faArrowAltCircleRight } from '@fortawesome/free-solid-svg-icons'

const Paginator = ({totalItemsCount,pageSize,currentPage,changeCurrentPage,portionSize=10, pagePortion = 1}) =>{
  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  let pages = [];

  let setCss = (i)=>{
    return `${currentPage === i ? css.selected : css.page}` 
  }


  // portion of pages
  let portionCount = Math.ceil(pagesCount/portionSize);
  const [portionNumber, setPortionNumber] = useState(1);

  useEffect(() => {
    setPortionNumber(pagePortion)
  }, [pagePortion])

  let leftPortionPageNumber = (portionNumber - 1) *  portionSize + 1;
  let rightPortionPageNumber = portionNumber *  portionSize ;
  

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  return(
    <div className={css.pages_wrapper}>
      {portionNumber === 1 ? null : 
      <FontAwesomeIcon onClick={()=>{setPortionNumber(portionNumber - 1 )}} className={css.icon} icon={faArrowAltCircleLeft} />}
      
        {pages
        .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
        .map(i => {
          return(  
            <span
                key = {i}
                onClick={() => { 
                  changeCurrentPage(i, portionNumber);  
                }}
                className={setCss(i)}
              >
                {i}
            </span>
          )
        })}

        {portionCount === portionNumber ? null : 
        <FontAwesomeIcon onClick={()=>{
          setPortionNumber(portionNumber + 1 )}} className={css.icon} icon={faArrowAltCircleRight} />}
       
    </div>
  )
}


export default Paginator